/* NavBar.css */
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    color: #ffffff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1000;
    transition: background-color 0.3s;
  }
  
  .light-mode .navbar {
    color: #000000;
  }
  
  .navbar-content {
    display: flex;
    gap: 20px;
  }
  
  .navbar-link {
    color: inherit;
    text-decoration: none;
  }
  
  .navbar-image {
    width: auto;
    height: 40px; /* Adjust the height as needed */
    border-radius: 5px;
  }

  .utc-clock {
    color: #ffffff;
    font-size: 14px;
    position: absolute;
    top: 10px; /* Adjust the top value based on your preference */
    left: 50%;
    transform: translateX(-50%);
  }

  .light-mode .utc-clock {
    color: #000000;
  }

  
  