/* Dropdown.css */
.dropdown {
    display: flex;
    align-items: center;
  }
  
  .dropdown-toggle {
    font-size: 20px;
    cursor: pointer;
    color: #ffffff;
    margin-right: 40px;
  }

  .light-mode .dropdown-toggle {
    color: #000000;
  }
  
  .dropdown-content {
    display: flex;
    flex-direction: column;
    background: linear-gradient(135deg, #f0f0f0, #e6e6e6, #dcdcdc, #d3d3d3);
    position: absolute;
    top: 45px;
    right: 0;
    border-radius: 5px;
    overflow: hidden;
    margin-right: 40px;
    margin-top: 20px;
  }

  .light-mode .dropdown-content {
    background: linear-gradient(135deg, #0c0c0c, #141414, #1c1c1c, #222222);
  }
  
  .dropdown-content div {
    padding: 10px;
    text-align: center;
    cursor: pointer;

  }
  

  