/* HexGrid.css */
.hex-grid-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
  }
  
  .hex-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    max-width: 1200px;
  }
  
  .hex-grid-item {
    position: relative;
    background-color: #ffffff;
    border-radius: 15px;
    overflow: hidden;
    transition: transform 0.3s ease-in-out;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .hex-grid-item:hover {
    transform: scale(1.05);
  }
  
  .hex-grid-image {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 15px 15px 0 0;
  }
  
  .hex-grid-content {
    padding: 15px;
  }
  
  .hex-grid-label {
    font-size: 1.2em;
    margin-top: 10px;
    color: #333333;
  }
  
  .hex-grid-link {
    text-decoration: none;
  }